// import { CustomerModel } from '../shared/customer.model';
import {AngularFirestoreCollection, AngularFirestore} from '@angular/fire/firestore'
// import { Observable } from 'rxjs/Observable';
// import firestore from 'firebase/firestore'
import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import { CustomerModel } from './customer.model';

@Injectable()
export class CustomerService {


    // itemsCollection: AngularFirestoreCollection<CustomerModel>;
    itemsCollection: AngularFirestoreCollection<CustomerModel>
    Message: Observable<CustomerModel[]>;
    date: Date = new Date();

    constructor(public afs: AngularFirestore) {
        this.Message = this.afs.collection('Resume_message').valueChanges();
    }
    submitComment(customers: CustomerModel) {
        console.log(customers);
        this.afs.collection('Resume_message').add({
            name: customers._name, message: customers._comment
            , phone: customers._phone, email: customers._email, date_added: this.date
        });
    }

    getItems() {
        return this.Message;
    }
}