export class CustomerModel {

    // dateAdded:any;
    id?: string;
    _name?: string;
    _email?: string;
    _phone?: string;
    _comment?: string;
    _guests?: number;
    _dateReserved?: string

    constructor(name?: string, email?: string, phone?: string, comment?: string,
        guests?: number, date?: string) {
        this._name = name;
        this._email = email;
        this._phone = phone;
        this._comment = comment;
        this._guests = guests;
        this._dateReserved = date
    }

}