import { Component, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { CustomerModel } from './customer.model';
import { CustomerService } from './customer.service';

@Component({
  selector: 'app-volunteer',
  templateUrl: './volunteer.component.html',
  styleUrls: ['./volunteer.component.css'],
  providers:[CustomerService]
})
export class VolunteerComponent implements OnInit {
  @ViewChild("form", { static: false }) form: NgForm;
  submited: Boolean;
  contactForm: any;
  customers: CustomerModel;

  constructor(private service: CustomerService) { }

  ngOnInit() {
    window.addEventListener("scroll", function (event) {
      var scroll = this.scrollY;
      // console.log(scroll)
      if (scroll === 0) {
          this.document.getElementById('btn').style.visibility = 'hidden';
      } else {
          this.document.getElementById('btn').style.visibility = 'visible';
      }
  });
  }

  onSubmit(form: NgForm) {
    var name = form.value.name;
    var phone = form.value.phone;
    var email = form.value.email;
    var comment = form.value.comment;

    this.customers = new CustomerModel(name, email, phone, comment,null,null);

    console.log(name);
    this.service.submitComment(this.customers);
    this.submited = true;
    this.form.reset();
  };

}
